.new-expense__controls {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
  }
  
  .new-expense__control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
  }
  
  .new-expense__control input {
    font: inherit;
    padding: 0.5rem;
    border: 1px solid #ccc;
    width: 20rem;
    max-width: 100%;
    border: none;
    border-bottom: 1px solid black;
  }
  ::placeholder { 
    color: black;
    opacity: 0.2;
    font-size: 15px;
  }
  
  .new-expense__actions {
    text-align: right;
  }